import React, { Component } from "react"
import { importAllFiles } from "../helpers/functions"
import { graphql, useStaticQuery } from "gatsby"
import GalleryTemplate from "../templates/GalleryTemplate"

const Decorations = () => {
  // state = {
  //   photos: importAllFiles(
  //     require.context(
  //       "../assets/photos/decorations",
  //       false,
  //       /\.(png|jpe?g|svg)$/
  //     )
  //   ),
  // }
  const data = useStaticQuery(graphql`
    {
      allFile(
        filter: {
          extension: { eq: "png" }
          sourceInstanceName: { eq: "decorations" }
        }
      ) {
        edges {
          node {
            publicURL
          }
        }
      }
    }
  `)

  return <GalleryTemplate pageName="Dekoracje" photos={data} />
}

export default Decorations
